.SectionCarts {
    width: 100%;
    margin-top: 30px;
    background-color: #f6f6f6;
    border-radius: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-top: 25px;
    
}
.SectionCarts a {
display: contents;
}

.logements {
    position: relative;
    width: 30%;
    aspect-ratio: 1/1;
    margin: 0 1% 3% 2%;
    border-radius: 20px;
    transition: transform .2s ease,-webkit-transform .2s
    
}

.imageCarts {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
}

.tittleCarts {
    position: absolute;
    bottom: 0.5rem;
    left: 0.5rem;
    color: #fff;
    font-weight: 500;
    font-family: "Montserrat";
}




  /* mobile*/

  @media screen and  (max-width: 767px) {
    .SectionCarts {
        flex-direction: column;
        background-color: white;
        margin-top: 0;
    }

    .logements {
        width: 100%;
        height: 280px;
        margin: 0;
        margin-bottom: 20px;
        
    }
    .imageCarts {
        border-radius: 15px;
    }
  }